/**
 * Component PasswordField is defined as
 * `<eui-base-v[version]-password-field>`
 *
 * Imperatively create component
 * @example
 * let component = new PasswordField();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-password-field></eui-base-v0-password-field>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import '../tooltip/Tooltip';
import style from './passwordField.css';

/**
 * @property {Boolean} autocomplete - true, browser automatically completes
 * the input values based on values that the user has entered before
 * @property {Boolean} autofocus - true, password-field should automatically
 * get focus when the page loads
 * @property {Boolean} disabled - true, disables the password field
 * @property {Boolean} fullwidth - true, sets the password field to
 * take the full width of the parent container
 * @property {Boolean} labelleft - true, sets the position of label to left
 * @property {String} labeltext - sets the label for the password field
 * @property {Number} maxlength - sets the maximum allowed length for the password field
 * @property {String} name - sets the name of the password field (used in a form)
 * @property {String} pattern - regular expression for validating input
 * @property {String} placeholder - sets the placeholder text to appear in the
 * password field when it is empty
 * @property {Boolean} readonly - true sets the password field readonly
 * @property {Boolean} required - true, sets the password field must be
 * filled out before submitting the form
 * @property {String} unmask - true, unmasks the password field
 * @property {String} validationMessage - message to display when input value is invalid
 * @property {Object} _inputnode - caches the rendered HTML <input/> element
 */
@definition(`eui-base-v${__VERSION__}-password-field`, {
  style,
  props: {
    autocomplete: { attribute: true, type: Boolean },
    autofocus: { attribute: true, type: Boolean },
    disabled: { attribute: true, type: Boolean },
    fullwidth: { attribute: true, type: Boolean },
    labelleft: { attribute: true, type: Boolean },
    labeltext: { attribute: true, type: String },
    maxlength: { attribute: true, type: Number },
    name: { attribute: true, type: String, default: 'item' },
    pattern: { attribute: true, type: String },
    placeholder: { attribute: true, type: String, required: true },
    readonly: { attribute: true, type: Boolean },
    required: { attribute: true, type: Boolean },
    unmask: { attribute: true, type: Boolean },
    validationMessage: { attribute: true, type: String },
  },
})
export default class PasswordField extends LitComponent {
  /**
   * Set custom validty on the text field by entering a message
   * when the field is invalid. When the field is valid enter an empty string.
   *
   * @function setCustomValidity
   * @param {String} message to display when the input is invalid, leave empty
   * to clear the error
   */
  setCustomValidity(message) {
    this.validationMessage = message;
    this._inputnode.setCustomValidity(message);
    this._checkValidity();
  }

  /**
   * Get the validity of the textfield.
   *
   * @property validity
   * @returns {ValidityState} - Object containing the validity state of the component.
   */
  get validity() {
    return this._inputnode.validity;
  }

  /**
   * Gets the value of the password filed
   */
  get value() {
    return this._inputnode.value;
  }

  /**
   * Sets the value of the password field
   * @param {string} value - value to be set on the password field
   */
  set value(value = null) {
    if (value === null) { return; }
    this._inputnode.value = String(value);
  }

  didUpgrade() {
    this._inputnode = this.shadowRoot.querySelector('input');
    if (this.pattern) {
      this.shadowRoot.querySelector('input').pattern = this.pattern;
    }
  }

   /**
   * Lifecycle callback executed each time the components props are updated
   *
   * @method didChangeProps
   * @param {Map} changedProps - previous values of the changed props
   *
   * @private
   */
  didChangeProps(changedProps) {
    const prevValue = changedProps.get('value');
    if (prevValue && prevValue !== this.value) {
      this.inputnode.value = this.value;
    }
    if (changedProps.has('pattern')) {
      this.shadowRoot.querySelector('input').pattern = this.pattern;
    }
  }

  /**
   * Handles click events on eye icon
   * @method _handleEyeClick
   * @private
   */
  _handleEyeClick() {
    if (!this.disabled) {
      this.unmask = !this.unmask;
    }
  }

  /**
   * Check the validity of the input field based on a pattern or custom validity if supplied
   * This function is calld each time the input event is triggered in the input field.
   *
   * @function _checkValidity
   */
  _checkValidity() {
    if (this._inputnode.checkValidity()) {
      this.shadowRoot.querySelector('.input-eye').classList.remove('password-invalid');
    } else {
      this.shadowRoot.querySelector('.input-eye').classList.add('password-invalid');
    }
  }

  /**
   * handle all events from the input field.
   *
   * @function handleEvent
   * @param {Event} event - event
   */
  handleEvent(event) {
    switch (event.type) {
      case 'blur':
      this.classList.remove('activeNode');
      break;

      case 'click':
      this.classList.add('activeNode');
      break;

      case 'input':
      this._checkValidity();
      break;

      default:
      break;
    }
  }

  /**
   * Render the password-field component. This function is called each time a
   * prop changes.
   */
  render() {
    const {
      unmask, labeltext, name, maxlength, placeholder,
      readonly, disabled, required, autofocus, autocomplete,
    } = this;

    const label = html`${labeltext ? html`<label>${labeltext}</label>` : ''}`;
    return html`<div class="input-label">
      ${label}
      <div class="input-eye active">
        <input
          type="${unmask ? 'text' : 'password'}"
          name="${name}"
          maxlength="${maxlength}"
          placeholder="${placeholder || (!labeltext ? 'set placeholder!' : '')}"
          autocomplete="${autocomplete ? 'on' : 'off'}"
          ?readonly="${readonly}"
          ?disabled="${disabled}"
          ?required="${required}"
          ?autofocus="${autofocus}"
          @click=${this}
          @blur=${this}
          @input=${this}
        />
        <eui-base-v0-tooltip message="${unmask ? 'Hide password' : 'Show password'}" position="bottom-end">
          <eui-base-v0-icon class="icon" name="${unmask ? 'eye-solid' : 'eye'}" @click=${() => this._handleEyeClick()}></eui-base-v0-icon>
        </eui-base-v0-tooltip>       
      </div>
      <span class="input__validation-error">${this.validationMessage}</span> 
    </div>`;
  }
}
