import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import { Spinner as EDSpinner } from '@eds/vanilla/number-field/Spinner';
import style from './spinner.css';

export class CustomSpinner extends EDSpinner {
  constructor(element) {
    super(element);
    this.dom.controls = element.querySelectorAll('.controls > eui-v0-icon');
    Object.defineProperty(this.dom, 'max', {
      get: () => element.querySelector('input').getAttribute('max') || Number.MAX_VALUE,
    });
    Object.defineProperty(this.dom, 'min', {
      get: () => element.querySelector('input').getAttribute('min') || -Number.MAX_VALUE,
    });
    Object.defineProperty(this.dom, 'step', {
      get: () => element.querySelector('input').getAttribute('step') || 1,
    });
  }

  setCorrectState() {
    this.dom.controls[0].classList.remove('disabled');
    this.dom.controls[1].classList.remove('disabled');

    if (this.dom.input.value === this.dom.min) {
      this.dom.controls[0].classList.add('disabled');
    }

    if (this.dom.input.value === this.dom.max) {
      this.dom.controls[1].classList.add('disabled');
    }
  }

  leadingZero(number) {
    const n = parseInt(number, 10);

    // if value isn't a number, default to 0
    if (Number.isNaN(n)) {
      return 0;
    }

    let result = n;
    if (n < 10 && super.zeroLeading) result = 0 + n;
    return result;
  }

  setCorrectFormat() {
    const input = this.dom.spinner.querySelector('input');
    if (!Number.isNaN(parseInt(input.value, 10))) {
      if (parseInt(input.value, 10) > this.dom.max) {
        input.value = this.dom.max;
      }
      if (parseInt(input.value, 10) < this.dom.min) {
        input.value = this.dom.min;
      }
    } else if (input.value.length > 0) {
      // not a number
      input.value = this.dom.min;
    }
  }
}

/**
 * @property {Number} max - number to set as a max value of input
 * @property {Number} min - number to set as a min value of input
 * @property {Number} step - number to set the step of input changing
 * @property {Number} value - number to set as a initial value of input
 */
@definition(`eui-base-v${__VERSION__}-spinner`, {
  style,
  props: {
    max: { attribute: true, type: Number, default: 10 },
    min: { attribute: true, type: Number, default: -10 },
    step: { attribute: true, type: Number, default: 2 },
    value: { attribute: true, type: Number, default: 0 },
  },
})
export default class Spinner extends LitComponent {
  constructor() {
    super();
    this.isButtonPressed = false;
    this.pressTimeout = 210;

    this.mousedown = this.mousedown.bind(this);
    this.mouseup = this.mouseup.bind(this);
  }

  /**
   * Sets value in between max and min value
   * @param {number} newValue Value you want to parse
   * @param {number} min Min value
   * @param {number} max Max value
   */
  _sanitizeValue(newValue, min, max) {
    const parsedNewValue = parseInt(newValue, 10);

    // if value isn't a number, default to 0
    if (Number.isNaN(parsedNewValue)) {
      return 0;
    }

    if (parsedNewValue < min) {
      return min;
    }

    if (parsedNewValue > max) {
      return max;
    }

    return parsedNewValue;
  }

  /**
   * @method mousedown
   * @description Function which fires then user press mouse button on arrow button
   *
   * @param {Event} event Mousedown event.
   */
  mousedown(event) {
    const { target } = event;
    const action = this.spinner[target.getAttribute('long-press-action')];

    this.isButtonPressed = true;
    this.timeout = setTimeout(() => {
      if (this.isButtonPressed) {
        action.call(this.spinner);
        this.mousedown({ target });
        if (this.pressTimeout > 20) {
          this.pressTimeout = this.pressTimeout - 20;
        }
      }
    }, this.pressTimeout);
  }

  /**
   * @method mousedown
   * @description Function which fires then user stop press button
   *
   * @method mouseup
   */
  mouseup() {
    this.isButtonPressed = false;
    this.pressTimeout = 210;
    clearTimeout(this.timeout);
  }

  handleEvent(event) {
    if (event.type === 'blur') {
      const inputVal = event.data === undefined ? this.shadowRoot.querySelector('input').value : event.data;
      this.value = this._sanitizeValue(inputVal, this.min, this.max);
    }
  }

  /**
   * lifecycle hook triggers when prop was changed
   *
   * @method didChangeProps
   *
   */
  didChangeProps(changedProps) {
    this.value = this._sanitizeValue(this.value, this.min, this.max);
    // If the value changed, bubble a change event up
    if (changedProps.get('value') !== this.value) {
      this.bubble('change', {
        value: this.value,
      });
    }
  }

  /**
   * Lifecycle callback executed when the component is rendered to the DOM
   *
   * @method didRender
   * @param {Object} changedProps - the previous values of the components props
   *
   * @private
   */
  didRender() {
    if (!this.spinner) {
      const spinnerContainer = this.shadowRoot.querySelector('.spinner');
      this.spinner = new CustomSpinner(spinnerContainer);
      spinnerContainer.addEventListener('changeTimerValue', () => {
        this.value = this.spinner.dom.input.value;
      });
      this.spinner.init();
    }

    if (this.spinner) {
      const domInput = this.spinner.dom.input;
      if (domInput.value !== this.value) {
        domInput.value = this.value;
      }

      this.spinner.setCorrectState();
    }
  }

  /**
   * @method didDisconnect
   * @description lifecycle hook triggers defore component will be disconnected
   */
  didDisconnect() {
    clearTimeout(this.timeout);
  }

  /**
   * Render the spinner component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`
      <div class="spinner">
        <label for="ns2">
          <slot></slot>
        </label>
        <input
          type="number"
          value="${this.value}"
          max="${this.max}"
          min="${this.min}"
          step="${this.step}"
          @blur="${this}"
        />
        <div class="controls">
          <eui-v0-icon
            class="icon"
            name="chevron-down"
            @mousedown=${this.mousedown}
            @mouseup=${this.mouseup}
            @mouseout=${this.mouseup}
            long-press-action="decreaseValue"
          ></eui-v0-icon>
          <eui-v0-icon
            class="icon"
            name="chevron-up"
            @mousedown=${this.mousedown}
            @mouseup=${this.mouseup}
            @mouseout=${this.mouseup}
            long-press-action="increaseValue"
          ></eui-v0-icon>
        </div>
      </div>
    `;
  }
}
