/**
 * Component Tooltip is defined as
 * `<eui-base-v[version]-tooltip>`
 *
 * Imperatively create component
 * @example
 * let component = new Tooltip();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-tooltip message="I am a tooltip">Hover over me</eui-base-v0-tooltip>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html, classMap } from '@eui/lit-component';
import style from './tooltip.css';

/**
 * @property {Boolean} dotted - sets the hovered item to be underlined dotted
 * @property {String} message - message displayed by the tooltip
 * @property {String} position - sets the position of the tooltip
 * @property {Boolean} visible - sets whether the tooltip should be visible always
 */
@definition(`eui-base-v${__VERSION__}-tooltip`, {
  style,
  props: {
    dotted: { attribute: true, type: Boolean },
    message: { attribute: true, type: String, default: '' },
    position: { attribute: true, type: String, default: 'bottom' },
    visible: { attribute: true, type: String },
  },
})
export default class Tooltip extends LitComponent {
  // this must overflow tooltip hiding animation time
  static HIDE_ANIMATION_TIME = 200;

  /**
   * Gets the list of classes that needs to be added on the
   * tooltip node.
   *
   * @method _getTooltipClassNames
   * @return {string} classNames - class names required to be on the tooltip node
   * @private
   */
  _getTooltipClassNames() {
    return classMap({
      tooltip: true,
      dotted: this.dotted,
    });
  }

  /**
   * Gets the list of classes that needs to be added on the
   * tooltip message node.
   *
   * @method _getTooltipClassNames
   * @return {string} classNames - class names required to be on the
   * tooltip message node
   * @private
   */
  _getMessageClassNames() {
    // Null check left for backward compatibility
    const visible = !(
          this.visible === 'always-hide'
          || this.visible === 'default' || this.visible === false
        )
        && (this.visible !== null);

    return classMap({
      message: true,
      [this.position]: true,
      visible,
      hidden: this.visible === 'always-hide',
    });
  }

  /**
   * Render the tooltip component. This function is called each time a
   * prop changes.
   */
  render() {
    const message = () => {
      if (!this.message || this.message.trim() === '') {
        return html`<div class="${this._getMessageClassNames()}">
        <slot name="message"></slot>
      </div>`;
      }
      return html`<div class="${this._getMessageClassNames()}">
      ${this.message}</div>`;
    };

    return html`
      <span class="${this._getTooltipClassNames()}">
        <slot></slot>
        ${message()}
      </span>`;
  }
}
