/**
 * Component Switch is defined as
 * `<eui-base-v[version]-switch>`
 *
 * Imperatively create component
 * @example
 * let component = new Switch();
 *
 * Declaratively create component
 * @example
 * <eui-base-v[version]-switch></eui-base-v[version]-switch>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import style from './switch.css';

/**
 * @property {String} labelOff - the label text displayed when the
 * switch is in the off state
 * @property {String} labelOn - the label text displayed when the
 * switch is in the on state
 * @property {Boolean} on - set the switch toggled on/off
 */
@definition(`eui-base-v${__VERSION__}-switch`, {
  style,
  props: {
    labelOff: { attribute: true, type: String, default: 'Off' },
    labelOn: { attribute: true, type: String, default: 'On' },
    on: { attribute: true, type: Boolean },
  },
})
export default class Switch extends LitComponent {
  /**
   * Hook into the lifecycle callback to trigger the change event when the
   * on prop is changed.
   *
   * @function didChangeProps
   * @param {Map} changedProps - previous values of the changed props
   */
  didChangeProps(changedProps) {
    if (changedProps.has('on')) {
      this.bubble('change', { on: this.on });
    }
  }

  /**
   * Handles the state change of the switch
   */
  handleEvent(event) {
    switch (event.type) {
      case 'change':
        this.on = !this.on;
        event.stopPropagation();
        break;

      default:
        event.stopPropagation();
        break;
    }
  }

  /**
   * Render the switch component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`
      <style type="text/css">
        :host * {
          box-sizing: border-box;
        }

        :host([on]) .switch .ball {
          background-color: var(--switch-background-selected,rgb(0, 130, 240));
          border-color: var(--switch-border-selected,rgb(0, 130, 240));
        }
        
        :host([on]) .switch .ball::before {
          -webkit-transform: translateX(13px);
          transform: translateX(13px);
          background-color: var(--switch-ball-selected,rgb(250, 250, 250));
        }
      </style>
      <label class="switch">
        <input 
          type="checkbox"
          ?checked=${this.on}
          @change="${this}"/>
        <i class="ball"></i>
        <span
          data-enabled=${this.labelOn}
          data-disabled=${this.labelOff}
          ></span>
      </label>
    `;
  }
}
