/**
 * Component dropdown is defined as
 * `<eui-base-v[version]-combo-box>`
 *
 * Imperatively create component
 * @example
 * let component = new ComboBox();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-combo-box></eui-base-v0-combo-box>
 *
 * @extends {Dropdown}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import style from './combo-box.css';
import keymap from '../../utils/keymap';

/**
 * @property {Array} data - data to configure the tree
 * @property {Boolean} disabled - disabled the dropdown button
 * @property {String} placeholder - set textfield placeholder attribute for combobox
 * @property {Boolean} visible - true makes menu visible
 * @property {String} width - allows to set fixed width for dropdown component
 */
@definition(`eui-base-v${__VERSION__}-combo-box`, {
  style,
  props: {
    data: { type: Array },
    disabled: { attribute: true, type: Boolean },
    placeholder: { attribute: true, type: String },
    visible: { type: Boolean },
    width: { attribute: true, type: String },
  },
})
export default class ComboBox extends LitComponent {
  constructor() {
    super();
    this.handleInputChange = this.handleInputChange.bind(this);
    this.unHighlightAll = this.unHighlightAll.bind(this);
    this.handleKeySelection = this.handleKeySelection.bind(this);
    this._getFilteredList = this._getFilteredList.bind(this);
    // handle position
    this.trackPositionInterval = null;
    this.position = {};
  }

  /**
   * Lifecycle callback executed when the component is connected to the DOM
   *
   * @method didConnect
   *
   */
  didConnect() {
    this.highlightIndex = -1;
    this.filteredList = null;
    this.menuItems = null;
    this._addClickedOutsideListener();
  }

  /**
   * lifecycle hook
   *
   * @method didUpgrade
   *
   */
  didUpgrade() {
    if (!this.inputField) {
      this.inputField = this.shadowRoot.querySelector('eui-base-v0-text-field');
    }
  }

  /**
   * Handles click events to determine if clicked outside the component
   * If so, then hide the dropdown if it is visible.
   *
   * @method _handleClickOutside
   * @params {MouseEvent} event - the event object
   * @private
   *
   */
  _handleClickOutside(event) {
    const path = event.path || (event.composedPath && event.composedPath());
    const hideDropdown = () => {
      this.hide();
      this.unHighlightAll();
      this.highlightIndex = -1;
    };
    if (this.visible && (path && !path.includes(this))) {
      hideDropdown();
    }
  }

  /**
   * a listener for Mouse Clicks outside the dropdown.
   *
   * @method _addClickedOutsideListener
   * @private
   *
   */
  _addClickedOutsideListener = () => {
    document.addEventListener('click', event => this._handleClickOutside(event), false);
  }

  /**
   * handle click event on dropdown menu
   * @method _handleMenuClick
   *
   * @private
   */
  _handleMenuClick = (event) => {
    const item = event.target;
    if (item.hasAttribute('menu-item')) {
      this._setInputFieldValue(item.textContent);
      this.hide();
      this.filteredList = this.menuItems;
      this.showFilteredList(this.filteredList);
      this.unHighlightAll();
    }
  }

  /**
   * create dropdown menu items
   * @method _makeDropdownOptions
   * @private
   */
  _makeDropdownOptions = () => this.data.map(item => html`<div menu-item tabindex="0" @click=${item.action}>${item.value}</div>`);

  /**
   * make dropdown menu visible
   * @method show
   *
   * @public
   */
  show = () => {
    if (this.disabled) {
      return;
    }
    this.alignMenuDirection();
    this._trackPosition();
      if (!this.visible) {
        this.visible = true;
      }
    // initialize menuItems when show first time
    if (!this.menuItems) {
      this.setMenuItems();
    }
  }

  /**
   * make dropdown menu hidden
   * @method hide
   *
   * @public
   */
  hide = () => {
    this.visible = false;
    this._stopTrackPosition();
  }

  /**
   * Tracks the position of the of the element and hides the
   * menu when it changes or other elements are clicked.
   *
   * @method _trackPosition
   * @private
   */
  _trackPosition() {
    // Create the interval. This interval will check frequently what
    // the current position is suppose to be.
    // If the position changes at all, then the component will disappear.
    if (!this.trackPositionInterval) {
      this.trackPositionInterval = setInterval(() => {
        const curPosition = this.getBoundingClientRect();
        if (this.position
          && (this.position.top !== curPosition.top || this.position.left !== curPosition.left)) {
          this.hide();
        }
      }, 1000 / 24);
    }
  }

  /**
   * clears the track position Interval.
   *
   * @method _stopTrackPosition
   * @private
   */
  _stopTrackPosition() {
    if (this.trackPositionInterval) {
      clearInterval(this.trackPositionInterval);
      delete this.trackPositionInterval;
    }
  }

  /**
   * return component's value
   *
   * @return {string}
   */
  get value() {
    return this.inputField.value;
  }

  /**
   * set input field value
   *
   * @param {string} newVal
   *
   */
  set value(newVal = '') {
    // If not a string, then just return
    if (typeof newVal !== 'string') { return; }
    this._setInputFieldValue(newVal);
    this.handleInputChange();
  }

  /**
   * set combo dropdown input textfield value
   * @function _setInputFieldValue
   * @private
   */
  _setInputFieldValue = (value) => {
    this.inputField.value = value;
    // trigger change event on value change
    this.bubble('combobox:changed', { value });
  }

  /**
   * filter menu-items based on text entered in input field
   * @function _getFilteredList
   *
   * @returns {Array} filtered item-list
   */
  _getFilteredList = () => {
    const inputValue = this.inputField.value ? this.inputField.value.toLowerCase() : '';
    const filteredList = this.menuItems.filter(
      item => item.textContent.toLowerCase().indexOf(inputValue) > -1,
    );
    return filteredList;
  }

  /**
   * handle display of filtered list
   *
   * @function showFilteredList
   *
   */
  showFilteredList(list) {
    // reset manu-item state before showing updated list
    this.highlightIndex = -1;
    this.unHighlightAll();

    // handle No Result Found scenario
    const menu = this.shadowRoot.querySelector('.menu');
    let emptyElem = menu.querySelector('.empty');
    if (!emptyElem) {
      // append a empty div- No Result found element
      const div = document.createElement('div');
      div.className = 'empty hidden';
      div.innerHTML = 'No Result Found';
      emptyElem = menu.appendChild(div);
    }
    this.menuItems.forEach(item => item.classList.add('hidden'));
    if (Array.isArray(list) && list.length === 0) {
      emptyElem.classList.remove('hidden'); // if filtered array is empty show empty div
    } else {
      emptyElem.classList.add('hidden');
      list.forEach(item => item.classList.remove('hidden'));
      list[0].classList.add('highlight');
      this.highlightIndex = 0;
    }
  }

  /**
   * handle value change in input textfield
   * @function handleInputChange
   * @public
   */
  handleInputChange = () => {
    if (!this.menuItems) {
      this.setMenuItems();
    }
    this.filteredList = this._getFilteredList();
    this.showFilteredList(this.filteredList);
  }

  /**
   * handle key press of Enter key
   * @function _onEnter
   *
   */
  _onEnter() {
    const currHighlightedItem = this.filteredList[this.highlightIndex];
    if (currHighlightedItem) {
      this._setInputFieldValue(currHighlightedItem.textContent);
      this.hide();
      this.filteredList = this.menuItems;
      this.showFilteredList(this.filteredList);
    }
  }

  /**
   * align Menu position based on available space
   * or based on menu-direction property
   * @method alignMenuDirection
   *
   */
  alignMenuDirection() {
    // set menu min-width same as component width
    const menu = this.shadowRoot.querySelector('.menu');
    if (menu) {
      menu.style.minWidth = `${this.offsetWidth}px`;
    }
    // calculate menu offsetwidth and height by displaying it for a fraction,
    // should not have any effect on element visibility
    menu.style.display = 'block';
    const menuWidth = menu.offsetWidth;
    const menuHeight = menu.offsetHeight;
    menu.style.display = null;

    // align vertical
    const rectObject = this.getBoundingClientRect();
    this.position = rectObject;
    const menuYPosition = rectObject.bottom + menuHeight; // menu.offsetHeight;

    if (menuYPosition > window.innerHeight) {
      const windowHeight = window.innerHeight;
      const topSpace = rectObject.top - 4;
      const bottomSpace = windowHeight - rectObject.bottom;
      if (topSpace > bottomSpace) {
        menu.style.bottom = `${windowHeight - topSpace}px`;
        menu.style.top = null;
      } else {
        menu.style.top = `${rectObject.bottom}px`;
        menu.style.bottom = null;
      }
    } else {
      menu.style.top = `${rectObject.bottom}px`;
      menu.style.bottom = null;
    }

    // handle menu's horizontal direction
    const menuXPosition = rectObject.left + menuWidth;
    const windowWidth = window.innerWidth;
    const spaceRight = windowWidth - rectObject.left;
    const spaceLeft = rectObject.right;
    if (menuXPosition > windowWidth) {
      if (spaceLeft > spaceRight) {
        menu.style.right = `${windowWidth - rectObject.right}px`;
        menu.style.left = null;
        menu.style.maxWidth = `${spaceLeft - 32}px`;
      } else {
        menu.style.left = `${rectObject.left}px`;
        menu.style.right = null;
        // reduce appx. 40px to maintain spacing from screen edge,
        // also to avoid menu flowing under vertical scroll bar
        menu.style.maxWidth = `${spaceRight - 40}px`;
      }
    } else {
      menu.style.left = `${rectObject.left}px`;
      menu.style.right = null;
      menu.style.maxWidth = `${spaceRight - 40}px`;
    }
  }

  /**
   * set the menu items in context
   * @method setMenuItems
   *
   * @public
   */
  setMenuItems = () => {
    const menuItems = !this.data
      ? this.querySelectorAll('*[menu-item]')
      : this.shadowRoot.querySelectorAll('*[menu-item]');
    if (menuItems && menuItems.length > 0) {
      this.menuItems = Array.from(menuItems);
    }
  }

  /**
   * handle key press events
   * @function handleKeySelection
   *.
   */
  handleKeySelection = (e) => {
    const menu = this.shadowRoot.querySelector('.menu');
    if (!this.menuItems) {
      this.setMenuItems();
    }
    if (!this.visible) {
      this.show();
    }
    if (!this.filteredList) {
      this.filteredList = this._getFilteredList();
    }
    // eslint-disable-next-line default-case
    switch (e.keyCode) {
      case keymap.ARROW_DOWN:
        if (this.highlightIndex === this.filteredList.length - 1) {
          this.highlightIndex = 0;
          menu.scrollTop = 0;
        } else {
          this.highlightIndex = this.highlightIndex + 1;
        }
        break;
      case keymap.ARROW_UP:
        if (this.highlightIndex > -1) {
          this.highlightIndex = Math.max(0, this.highlightIndex - 1);
        } else {
          this.highlightIndex = this.filteredList.length - 1;
        }
        break;
      case keymap.ENTER:
        this._onEnter();
        break;
    }
    const currentHighlightedItem = this.filteredList[this.highlightIndex];
    if (currentHighlightedItem) {
      // highlight the current indexed item
      this.unHighlightAll();
      currentHighlightedItem.classList.add('highlight');
      // move scrollbar position if current highligted item is hidden in scroll
      if (
        currentHighlightedItem.offsetTop + currentHighlightedItem.offsetHeight
        > menu.clientHeight
      ) {
        menu.scrollTop = currentHighlightedItem.offsetTop
          + currentHighlightedItem.offsetHeight
          - menu.clientHeight;
      }
    }
  }

  /**
   * handle key chevron click
   * @function handleChevronClick
   *.
   */
  handleChevronClick = (event) => {
    if (this.visible) {
      event.stopPropagation();
      this.hide();
    }
  };


  /**
   * unhighlight all items in menulist
   * @function unHighlightAll
   *
   */
  unHighlightAll = () => {
    if (!this.menuItems) {
      this.setMenuItems();
    }
    this.menuItems.forEach(item => item.classList.remove('highlight'));
  }

  render() {
    const isDisabled = this.disabled ? 'disabled' : '';
    const inlineWidth = this.width ? `width:${this.width}` : '';
    const inputComboField = html`
      <eui-base-v0-text-field
        placeholder="${this.placeholder || 'Write or select...'}"
        ?fullwidth="${!!this.width}"
        ?disabled=${isDisabled}
        @click="${this.show}"
        @input="${this.handleInputChange}"
        @keyup="${this.handleKeySelection}"
      >
      <eui-v0-icon
        slot="icon"
        name="chevron-down"
        @click="${this.handleChevronClick}">
      </eui-v0-icon>
      </eui-base-v0-text-field>
    `;

    const btnDisplayClass = this.visible ? '' : 'menu--hidden';
    return html`
      <div class="dropdown" style=${inlineWidth}>
        ${inputComboField}
        <div class="menu ${btnDisplayClass}" @click="${this._handleMenuClick}">
          ${!this.data
            ? html`
                <slot></slot>
              `
            : this._makeDropdownOptions()}
        </div>
      </div>
    `;
  }
}
