/**
 * Component dropdown with ellipsis is defined as
 * `<eui-base-v[version]-dropdown-ellipsis>`
 *
 * Imperatively create component
 * @example
 * let component = new DropdownEllipsis();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-dropdown-ellipsis></eui-base-v0-dropdown-ellipsis>
 *
 * @extends {Dropdown}
 */
import { definition } from '@eui/component';
import style from './dropdown-ellipsis.css';
import Dropdown from '../dropdown/Dropdown';

/**
 * @property {boolean} more - if true, replaces dropdown button with more Icon
 */
@definition(`eui-base-v${__VERSION__}-dropdown-ellipsis`, {
  style,
  props: {
    more: { attribute: true, type: 'boolean', default: true },
  },
})
export default class DropdownEllipsis extends Dropdown {}
