/**
 * Component Dropdown Menu is defined as
 * `<eui-base-v[version]-dropdown>`
 *
 * Imperatively create component
 * @example
 * let component = document.createElement('eui-base-v[version]-dropdown')
 *
 * Declaratively create component
 * @example
 * <eui-base-v[version]-dropdown></eui-base-v[version]-dropdown>
 *
 * @extends {LitComponent}
 */
import { LitComponent, html } from '@eui/lit-component';
import { definition } from '@eui/component';
import style from './dropdown.css';
import '../checkbox/Checkbox';
import '../radio-button/RadioButton';
import dropdownTypes from './dropdownTypes';

/**
 * @property {Array} data - data to configure the tree
 * @property {String} dataInnerlabel - label of dropdown button on selecting
 * checkbox menu item when data type is multi
 * @property {String} dataType - define if dropdown contains actions,
 * single select or multi select
 * @property {String} disabled - disabled the dropdown button
 * @property {String} label - sets the label of dropdown button
 * @property {Boolean} more - if true, replaces dropdown button with more Icon
 * @property {Boolean} visible - true makes menu visible
 * @property {String} width - allows to set fixed width for dropdown component
 */
@definition(`eui-base-v${__VERSION__}-dropdown`, {
  style,
  props: {
    data: { type: Array },
    dataInnerlabel: { attribute: true, type: String },
    dataType: { attribute: true, type: String, default: dropdownTypes.CLICK },
    disabled: { attribute: true, type: Boolean },
    label: { attribute: true, type: String, required: true },
    _dynamicLabel: { type: String },
    more: { attribute: true, type: Boolean },
    visible: { type: Boolean },
    width: { attribute: true, type: String },
  },
})
export default class Dropdown extends LitComponent {
  /**
   * dropdown constructor.
   */
  constructor() {
    super();
    this.toggleDropdownMenu = this.toggleDropdownMenu.bind(this);
    this._selectSingleMenuItems = this._selectSingleMenuItems.bind(this);
    this._selectMultiMenuItems = this._selectMultiMenuItems.bind(this);
    this._addEventListners = this._addEventListners.bind(this);
    this._addClickedOutsideListener = this._addClickedOutsideListener.bind(this);
    this._resetRadioButtons = this._resetRadioButtons.bind(this);
    // handle position
    this.trackPositionInterval = null;
    this.position = {};
  }

  /**
   * Lifecycle callback executed when the component is connected to the DOM
   *
   * @method didConnect
   *
   */
  didConnect() {
    this._dynamicLabel = this.label;
    this._addEventListners();
    this._addClickedOutsideListener();
    this._dynamicLabel = this.label;
    if (this.dataType === 'multi') {
      if (this.data) {
        const selectedItems = this.data.filter(item => item.checked).length;
        if (selectedItems > 0) {
          this._dynamicLabel = `${this.dataInnerlabel} (${selectedItems})`;
        } else {
          this._dynamicLabel = this.label;
        }
      }
      if (!this.data) {
        const selectedItems = [...this.childNodes].filter(node => node.checked).length;
        if (selectedItems > 0) {
          this._dynamicLabel = `${this.dataInnerlabel} (${selectedItems})`;
        } else {
          this._dynamicLabel = this.label;
        }
      }
    }
  }

  /**
   * Lifecycle hook to hook into the point where the component is upgraded.
   * This is a good time to start observing the menu for changes to it's items.
   *
   * @function didUpgrade
   */
  didUpgrade() {
    this._observeMenu();
  }

  /**
   * Lifecycle hook to hook into the point where the component has been deleted from the DOM.
   * This is a good point to disconnect from the menu observer.
   *
   * @function didDisconnect
   */
  didDisconnect() {
    if (this.observer != null) {
      this.observer.disconnect();
    }
  }

  /**
   * Compare two data arrays. This is used to compare prev.data and
   * this.data to determine if a render is required.
   * This function compares the key/value pairs for each menu item.
   *
   * @function _isEqual
   * @param {Array} array1 - the first array to compare
   * @param {Array} array2 - the second array to compare
   */
  _isEqual(array1, array2) {
    if (array1 == null || array2 == null) {
      return false;
    }

    if (array1.length !== array2.length) {
      return false;
    }

    return array1.every((item, index) => {
      if (Object.keys(item).length !== Object.keys(array2[index]).length) {
        return false;
      }
      return Object.keys(item).every(key => item[key] === array2[index][key]);
    });
  }

  /**
   * lifecycle hook to hook into changes to props.
   *
   * @function didChangeProps
   * @param {Object} changedProps - map containing changed props
   */
  didChangeProps(changedProps) {
    if (changedProps.has('label')) {
      if (this.dataType === dropdownTypes.MULTI) {
        this._selectMultiMenuItems();
      } else if (this.dataType === dropdownTypes.SINGLE) {
        if (this.selectedItems.length === 0) {
          this._dynamicLabel = this.label;
        }
      } else if (this.dataType === dropdownTypes.CLICK) {
        this._dynamicLabel = this.label;
      }
    }

    if (this.data && this.dataType === 'single' && !this._isEqual(changedProps.get('data'), this.data)) {
      const checkedRadio = this.data.filter(node => node.checked);
      if (checkedRadio.length > 0) {
        this._dynamicLabel = checkedRadio[0].value;
      } else {
        this._dynamicLabel = this.label;
      }
    }
  }

  /**
   * a listener for Mouse Clicks outside the dropdown.
   *
   * @method _addClickedOutsideListener
   *
   */
  _addClickedOutsideListener = () => {
    document.addEventListener(
      'mouseup',
      (event) => {
        const hideDropdown = () => {
          this.hide();
        };
        if (this.visible) {
          try {
            if (event.path.indexOf(this) === -1) {
              hideDropdown();
            }
          } catch (error) {
            if (!this.contains(event.target)) {
              hideDropdown();
            }
          }
        }
      },
      false,
    );
  };

  /**
   * make dropdown menu visible
   * @method show
   *
   * @public
   */
  show = () => {
    if (this.disabled) {
      return;
    }
    this.alignMenuDirection();
    this._trackPosition();
    if (!this.visible) {
      this.visible = true;
    }
  };

  /**
   * make dropdown menu hidden
   * @method hide
   *
   * @public
   */
  hide = () => {
    this.visible = false;
    this._stopTrackPosition();
  };

  /**
   * Tracks the position of the of the element and hides the
   * menu when it changes or other elements are clicked.
   *
   * @method _trackPosition
   * @private
   */
  _trackPosition() {
    // Create the interval. This interval will check frequently what
    // the current position is suppose to be.
    // If the position changes at all, then the component will disappear.
    if (!this.trackPositionInterval) {
      this.trackPositionInterval = setInterval(() => {
        const curPosition = this.getBoundingClientRect();
        if (this.position.top !== curPosition.top || this.position.left !== curPosition.left) {
          this.hide();
        }
      }, 1000 / 24);
    }
  }

  /**
   * clears the track position Interval.
   *
   * @method _stopTrackPosition
   * @private
   */
  _stopTrackPosition() {
    if (this.trackPositionInterval) {
      clearInterval(this.trackPositionInterval);
      delete this.trackPositionInterval;
    }
  }

  /**
   * align Menu position based on available space
   * or based on menu-direction property
   * @method alignMenuDirection
   *
   */
  alignMenuDirection() {
    // set menu min-width same as component width
    const menu = this.shadowRoot.querySelector('.menu');
    if (menu) {
      menu.style.minWidth = `${this.offsetWidth}px`;
    }
    // calculate menu offsetwidth and height by displaying it for a fraction,
    // should not have any effect on element visibility
    menu.style.display = 'block';
    const menuWidth = menu.offsetWidth;
    const menuHeight = menu.offsetHeight;
    menu.style.display = null;

    // align vertical
    const rectObject = this.getBoundingClientRect();
    this.position = rectObject;
    const menuYPosition = rectObject.bottom + menuHeight; // menu.offsetHeight;

    if (menuYPosition > window.innerHeight) {
      const windowHeight = window.innerHeight;
      const topSpace = rectObject.top - 4;
      const bottomSpace = windowHeight - rectObject.bottom;
      if (topSpace > bottomSpace) {
        menu.style.bottom = `${windowHeight - topSpace}px`;
        menu.style.top = null;
      } else {
        menu.style.top = `${rectObject.bottom}px`;
        menu.style.bottom = null;
      }
    } else {
      menu.style.top = `${rectObject.bottom}px`;
      menu.style.bottom = null;
    }

    // handle menu's horizontal direction
      const menuXPosition = rectObject.left + menuWidth;
      const windowWidth = window.innerWidth;
      const spaceRight = windowWidth - rectObject.left;
      const spaceLeft = rectObject.right;
      if (menuXPosition > windowWidth) {
        if (spaceLeft > spaceRight) {
          menu.style.right = `${windowWidth - rectObject.right}px`;
          menu.style.maxWidth = `${spaceLeft - 32}px`;
        } else {
        menu.style.left = `${rectObject.left}px`;
        // reduce appx. 40px to maintain spacing from screen edge,
        // also to avoid menu flowing under vertical scroll bar
        menu.style.maxWidth = `${spaceRight - 40}px`;
        }
     } else {
      menu.style.left = `${rectObject.left}px`;
      menu.style.maxWidth = `${spaceRight - 40}px`;
     }
  }

  /**
   * toggle the dropdown Menu
   * @method toggleDropdownMenu
   *
   * @public
   */
  toggleDropdownMenu = (event) => {
    event.stopPropagation();
    if (this.disabled) {
      return;
    }
    if (this.visible) {
      this.hide();
    } else {
      this.show();
    }
  };

  /**
   * set the menu items in context
   * @method setMenuItems
   *
   * @public
   */
  setMenuItems = () => {
    const menuItems = !this.data
      ? this.querySelectorAll('*[menu-item]')
      : this.shadowRoot.querySelectorAll('*[menu-item]');
    if (menuItems && menuItems.length > 0) {
      this.menuItems = Array.from(menuItems);
    }
  };

  /**
   * handle single select radio button menu items
   * @method _selectSingleMenuItems
   *
   * @private
   */
  _selectSingleMenuItems = (event) => {
    this._dynamicLabel = event.detail.value === '' ? this._dynamicLabel : event.detail.value;
    this.label = this._dynamicLabel;
    this.hide();
  };

  /**
   * handle multi select - Checkboxes menu tems
   * @method _selectMultiMenuItems
   *
   * @private
   */
  _selectMultiMenuItems = () => {
    if (this.selectedItems.length) {
      this._dynamicLabel = `${this.dataInnerlabel} (${this.selectedItems.length})`;
    } else {
      this._dynamicLabel = this.label;
    }
  };

  /**
   * handle click event on dropdown menu
   * @method _handleMenuClick
   *
   * @private
   */
  _handleMenuClick = () => {
    if (this.dataType === dropdownTypes.CLICK) {
      this.hide();
    }
  };

  /**
   * Get an array of the selected items.
   *
   * @method selectedItems
   * @returns {Array} selected items
   * @deprecated use value instead.
   */
  get selectedItems() {
    let items = [];
    if (this.data) {
      if (this.data !== []) {
        if (this.shadowRoot) {
          items = [].slice.call(this.shadowRoot.querySelectorAll('*[menu-item]'));
          items = items.filter(item => item.hasAttribute('checked') === true);
        } else {
          items = this.data.filter(item => item.checked);
        }
      }
    } else {
      items = !this.data
        ? [].slice.call(this.querySelectorAll('*[menu-item]'))
        : [].slice.call(this.shadowRoot.querySelectorAll('*[menu-item]'));
      items = items.filter(item => item.hasAttribute('checked') === true);
    }
    return items;
  }

  /**
   * Get the value of the dropdown. depending on the data-type this
   * function returns either an Array.
   * data-type = 'multi/single' returns an array.
   *
   * @property value
   * @returns {Array/String}
   * @public
   */
  get value() {
    let items = [];
    if (this.data) {
      if (this.data !== []) {
        if (this.shadowRoot) {
          items = [].slice.call(this.shadowRoot.querySelectorAll('*[menu-item]'));
          items = items.filter(item => item.hasAttribute('checked') === true);
        } else {
          items = this.data.filter(item => item.checked);
        }
      }
    } else {
      items = !this.data
        ? [...this.querySelectorAll('*[menu-item]')]
        : [...this.shadowRoot.querySelectorAll('*[menu-item]')];
      items = items.filter(item => item.hasAttribute('checked') === true);
    }
    return items;
  }

  /**
   * Observe the menu for changes to it's items. In cases where the menu item's lable changes the
   * dropdown must be informed so it can change the label of the selected item in the button label
   *
   * @function _observeMenu
   * @private
   */
  _observeMenu() {
    const config = { characterData: true, childList: true, subtree: true };
    if (!this.observer) {
      // Create an observer instance to observe menu items modified in the menu.
      this.observer = new window.MutationObserver((mutationsList) => {
        const menuItemsChanged = mutationsList.some(mutation => mutation.type === 'characterData');
        if (this.dataType === 'single' && menuItemsChanged) {
          const selected = [
            ...this.querySelectorAll(`eui-base-v${__VERSION__}-radio-button`),
          ].filter(node => node.checked);
          if (selected.length === 0) {
            this._dynamicLabel = this.label;
          } else {
            this._dynamicLabel = selected[0].value;
          }
        }
      });
    }
    // Start observing the dropdown mutations
    this.observer.observe(this, config);
  }

  /**
   * handle dropdown menu events
   * @method _addEventListners
   *
   * @private
   */
  _addEventListners = () => {
    /* eslint default-case:0 */
    switch (this.dataType) {
      case dropdownTypes.SINGLE:
        this.addEventListener('change', event => this._selectSingleMenuItems(event), false);
        break;
      case dropdownTypes.MULTI:
        this.addEventListener('change', () => this._selectMultiMenuItems(), false);
        break;
    }
  };

  /**
   * handle impertaively created default dropdown's click event
   * @method _handleImperativeMenuItem
   * @private
   *
   * @param {Object} event - event from menu-item
   * @param {Object} item - menu-item object
   */
  _handleImperativeMenuItem(event, item) {
    if (item.disabled) {
      this._preventClick(event);
      return;
    }
    if (typeof item.action === 'function') {
      item.action();
    }
  }

  /**
   * create dropdown menu items
   * @method _makeDropdownOptions
   * @private
   */
  _makeDropdownOptions = () => {
    const groupName = Math.random();
    return this.data.map(item => this._makeMenuItemFromData(this.dataType, item, groupName));
  };

  /**
   * create dropdown menu item
   * @method _makeMenuItemFromData
   * @param {String} type - type of menu item
   * @param {Object} menuItem - menu-item Object
   * @private
   */
  _makeMenuItemFromData(type, menuItem, group = 'menu-item') {
    let item;
    /* eslint default-case:0 */
    switch (type) {
      case dropdownTypes.CLICK: {
        item = html`
          <div
            menu-item
            tabindex="0"
            @click=${event => this._handleImperativeMenuItem(event, menuItem)}
            ?disabled=${menuItem.disabled}
          >
            ${menuItem.value}
          </div>
        `;
        break;
      }
      case dropdownTypes.SINGLE: {
        item = html`
          <eui-base-v0-radio-button
            menu-item
            @change="${(event) => {
              this._resetRadioButtons();
              menuItem.checked = event.detail.checked;
            }}"
            name="${menuItem.name}"
            group=${group}
            ?checked=${menuItem.checked}
            ?disabled=${menuItem.disabled}
          >
            ${menuItem.value}
          </eui-base-v0-radio-button>
        `;
        break;
      }
      case dropdownTypes.MULTI: {
        item = html`
          <eui-base-v0-checkbox
            menu-item
            @change="${(event) => {
              menuItem.checked = event.detail.checked;
            }}"
            name="${menuItem.name}"
            ?checked="${menuItem.checked}"
            ?disabled=${menuItem.disabled}
          >
            ${menuItem.value}
          </eui-base-v0-checkbox>
        `;
        break;
      }
    }
    return item;
  }

  /**
   * Reset the data relating to the radio buttons
   * @function _resetRadioButtons
   * @private
   */
  _resetRadioButtons = () => {
    // iterate over all data items and set them unchecked
    if (this.data) {
      this.data.forEach((item) => {
        item.checked = false;
      });
    }
  }

  /**
   * prevent default click event
   * @function _preventClick
   * @param {Object} event - event from menu-item
   */
  _preventClick(event) {
    event.stopPropagation();
    event.preventDefault();
  }

  /**
   * listener for slotchange event
   * @function _handleSlotChangeEvent
   *
   */
  _handleSlotChangeEvent() {
    if (this.dataType === 'multi') {
      this._selectMultiMenuItems();
    }
    if (this.dataType === 'single') {
      const selected = [...this.childNodes].filter(node => node.checked);
      if (selected.length === 0) {
        this._dynamicLabel = this.label;
      } else {
        this._dynamicLabel = selected[0].value;
      }
    }
    if (this.dataType === 'click') {
      const disabledItems = [...this.children].filter(node => node.hasAttribute('disabled'));
      disabledItems.forEach((element) => {
        element.addEventListener('click', e => this._preventClick(e));
      });
    }
  }

  /**
   * Render the <eui-base-v[version]-dropdown> component. This function is called each time a
   * prop changes.
   *
   * @method render
   */
  render() {
    const isDisabled = this.disabled ? 'disabled' : '';
    const inlineWidth = this.width ? `width:${this.width}` : '';

    const dropdownButton = this.more
      ? html`
          <eui-base-v0-icon
            class="more"
            name="more"
            @click="${this.toggleDropdownMenu}"
            ?disabled=${isDisabled}
          >
          </eui-base-v0-icon>
        `
      : html`
          <eui-base-v0-button
            @click="${this.toggleDropdownMenu}"
            ?disabled=${isDisabled}
            icon="chevron-down"
            reverse
            align-edge
            style=${inlineWidth}
          >
            ${this._dynamicLabel}
          </eui-base-v0-button>
        `;

    const btnDisplayClass = this.visible ? '' : 'menu--hidden';
    return html`
      <div class="dropdown" style=${inlineWidth}>
        ${dropdownButton}
        <div class="menu ${btnDisplayClass}" @click="${this._handleMenuClick}">
          ${!this.data
            ? html`
                <slot @slotchange=${() => this._handleSlotChangeEvent()}></slot>
              `
            : this._makeDropdownOptions()}
        </div>
      </div>
    `;
  }
}
