/**
 * Component Slider is defined as
 * `<eui-base-v[version]-slider>`
 *
 * Imperatively create component
 * @example
 * let component = new Slider();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-slider></eui-base-v0-slider>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import { Slider as EDSSlider } from '@eds/vanilla/number-field/Slider';
import style from './slider.css';

/**
 * @property {number} from - start of the number range
 * @property {number} to - end of the number range
 * @property {number} step - step value to move the slider
 * @property {number} value - nitial value of the slider
 * @property {boolean} fullwidth - maximum width of the slider
 */
@definition(`eui-base-v${__VERSION__}-slider`, {
  style,
  props: {
    from: { attribute: true, type: Number, default: 0 },
    to: { attribute: true, type: Number, default: 100 },
    step: { attribute: true, type: Number, default: 10 },
    value: { attribute: true, type: Number, default: 0 },
    fullwidth: { attribute: true, type: Boolean },
  },
})
export default class Slider extends LitComponent {
  /**
   * @function didChangeProps
   * @description lifecycle hook trigger when component receives a new prop.
   */
  didChangeProps() {
    if (this.edsSlider) {
      this.edsSlider.dom.input.value = this.value;
      this.bubble('change', {
        value: this.value,
      });
    }
  }

  /**
   * Lifecycle callback executed when the component is rendered to the DOM
   *
   * @method didRender
   * @param {Map} changedProps - the previous values of the components props
   *
   * @private
   */
  didRender(changedProps) {
    super.didRender(changedProps);

    if (!this.edsSlider) {
      this.edsSlider = new EDSSlider(this.shadowRoot.querySelector('.slider'));
      this.edsSlider.updateValueHolder = () => {
        this.value = this.edsSlider.dom.input.value;
      };
      this.edsSlider.init();
    }
  }

  /**
   * Render the slider component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`
      <div class="slider">
        <label for="slider1">
          <slot></slot>
        </label>
        <input
          type="range"
          step=${this.step}
          min=${this.from}
          max=${this.to}
          value=${this.value}
          id="slider1"
        />
        <span class="value">${this.value}</span>
        <div class="limits">
          <span class="from">${this.from}</span>
          <span class="to">${this.to}</span>
        </div>
      </div>
    `;
  }
}
