import { LitComponent, html } from '@eui/lit-component';
import { definition } from '@eui/component';
import { Datepicker as EDSDatepicker } from '@eds/vanilla/date-field/Datepicker';
import style from './datepicker.css';

import '../text-field/TextField';

/**
 * CustomDatepicker is used to customise the internals of the Datepicked supplied by EDS.
 *
 * @class CustomDatepicker
 * @extends EDSDatepicker
 */
class CustomDatepicker extends EDSDatepicker {
  constructor(element) {
    super(element);
    this.dom.input = element.querySelector('eui-base-v0-text-field[type="text"]');
    this.dom.hiddenInput = element.querySelector('input[type="hidden"]');
  }

  /**
   * Handler for clicks outside the component. The calendar should be hidden if the
   * user clicks outside the component
   *
   * @override ifClickedOutside
   * @param {Event} event
   */
  ifClickedOutside(event) {
    const path = event.path || (event.composedPath && event.composedPath());
    if (path && !path.includes(this.dom.datepicker)) {
      this.hide();
    }
  }

  /**
   * When an invalid date is entered, the date should be set to today's date.
   *
   * @override invalidDateFormat
   */
  invalidDateFormat() {
    super.invalidDateFormat();
    this.dom.input.dispatchEvent(new Event('change'));
  }

  /**
   * TODO: contributed this bug fix back to EDS. Remove this function once the bug is fixed on EDS.
   * Bug Reported: https://gitlab.seld.rnd.ericsson.se/eds/eds/issues/56
   * Set the selected date in the calendar displayed on the screen.
   *
   * @override setSelectedDateCSS
   */
  setSelectedDateCSS() {
    this.calendar.removeAllSelectedDays();

    // in case .selectedDate is null, fallback to input value in the dom:
    const selectedDay = !this.selectedDate
      ? this.selectedDate.day
      : `${new Date(this.dom.input.value).getDate()}`;

    Array.from(this.dom.days).some((day) => {
      if (day.innerText === selectedDay) {
        day.classList.add('selected');
        return true;
      }
      return false;
    });
  }

  /**
   * Added options to bubbling event.
   *
   * @override selectDay
   */
  selectDay(event) {
    // TODO: Is this just for testing or should this check be in production?
    if (!event.target.classList.contains('dummy')) {
      this.calendar.removeAllSelectedDays();
      this.selectedDate = {
        year: this.calendar.calendar.year,
        month: this.calendar.calendar.month,
        day: event.target.innerText,
      };
      const formattedDate = this.calendar.formatDate(this.selectedDate);
      this.dom.hiddenInput.value = formattedDate;
      this.dom.input.value = formattedDate;
      if (event.target.tagName === 'TD') {
        event.target.classList.add('selected');
      } else {
        event.target.parentNode.classList.add('selected');
      }
      this.dom.input.dispatchEvent(new Event('change', { bubbles: true, composed: true }));
      this.hide();
    }
  }
}

/**
 * @property {String} date - date to set in format YYYY-MM-DD
 */
@definition(`eui-base-v${__VERSION__}-datepicker`, {
  style,
  props: {
    date: { attribute: true, type: String, default: '' },
  },
})
export default class Datepicker extends LitComponent {
  /**
   * lifecycle hook
   *
   * @method didUpgrade
   *
   */
  didUpgrade() {
    if (!this.datepicker) {
      this.datepicker = new CustomDatepicker(this.shadowRoot.querySelector('.datepicker'));
      this.datepicker.init();
    }
    this.datepicker.dom.input.value = this.date;
    this.datepicker.listenInput();
  }

  /**
   * lifecycle hook
   *
   * @method didChangeProps
   * @param {Map} changedProps - previous values of the props
   */
  didChangeProps(changedProps) {
    if (changedProps.has('date')) {
      this.datepicker.dom.input.value = this.date;
      this.datepicker.listenInput();
    }
  }

  /**
   * handles change/input events
   *
   * @method handleEvent
   * @param {Object} event
   */
  handleEvent(event) {
    if (event.type === 'change' || event.type === 'input') {
      this.date = event.target.value;
    }
  }

  /**
   * Render the datepicker component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`
      <div class="datepicker">
        <eui-base-v0-text-field
          type="text"
          placeholder="YYYY-MM-DD"
          name="datepicker"
          maxlength="10"
          class="with-icon"
          @change=${this}
          @input=${this}
        >
          <eui-v0-icon
            slot="icon"
            style="display: block; margin: auto"
            name="calendar"
          ></eui-v0-icon>
        </eui-base-v0-text-field>
        <div class="suffix icon-inside clickable"><i class="icon icon-calendar"></i></div>

        <div class="calendar closed">
          <div class="head">
            <i>
              <eui-base-v0-icon name="chevron-left" size="16px"></eui-base-v0-icon>
            </i>
            <span class="month"></span>
            <i>
              <eui-base-v0-icon name="chevron-right" size="16px"></eui-base-v0-icon>
            </i>
            <i>
              <eui-base-v0-icon name="chevron-left" size="16px"></eui-base-v0-icon>
            </i>
            <span class="year"></span>
            <i>
              <eui-base-v0-icon name="chevron-right" size="16px"></eui-base-v0-icon>
            </i>
          </div>
          <table class="body"></table>
        </div>
        <input type="hidden" name="selectedDay" value="" />
      </div>
    `;
  }
}
