/**
 * Component Tree is defined as
 * `<eui-base-v[version]-tree>`
 *
 * Imperatively create component
 * @example
 * let component = new Tree();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-tree></eui-base-v0-tree>
 *
 * @extends {LitComponent}
 */
import { LitComponent, html, repeat } from '@eui/lit-component';
import { definition } from '@eui/component';
import style from './tree.css';
import '../tree-item/TreeItem';

@definition(`eui-base-v${__VERSION__}-tree`, {
  style,
  props: {
    data: { type: Array },
    navigation: { attribute: true, type: Boolean },
  },
})
export default class Tree extends LitComponent {
  /**
   * This is used to recursively set all tree items inactive.
   * This is only used when the Tree is setup using data prop.
   *
   * @function deactivateAllItems
   * @param {Object} data - data representing the tree item
   */
  deactivateAllItems(data = this.data) {
    data.forEach((item) => {
      if (item.children) {
        item.active = false;
        // deactivate all children...
        this.deactivateAllItems(item.children);
      } else {
        item.active = false;
      }
    });
  }

  /**
   * This handles all events from the Tree.
   * When the "deactive-all" event is received it first deactivates all tree
   * items, then sets the desired tree item active. Once the tree item is set
   * active it mutates the data prop.
   * The mutation is caught by "didChangeProps".
   *
   * @function handleEvent
   * @param {Event} event - events from the Tree
   */
  handleEvent(event) {
    if (event) {
      event.stopPropagation();
    }

    if (event.type === 'deactive-all') {
      if (this.data) {
        this.deactivateAllItems();
      } else {
        this.deactivateAll();
      }
      if (this.data) {
        event.detail.itemData.active = true;
        this.data = [...this.data];
      } else {
        event.detail.active = true;
      }
    }
  }

  /**
   * This function is used to set the active prop of the parent of the active tree item.
   * It is called recursively starting at the top most level and ending
   * when it reaches the actual active tree item.
   *
   * @function _setActive
   * @param {Object} data - data representing tree item.
   * @private
   */
  _setActive(data = this.data) {
    let foundActivityAtIndex = 0;
    const foundItem = data.some((item, index) => {
      const dataString = JSON.stringify(item);
      const found = dataString.includes('"active":true');
      if (found) {
        foundActivityAtIndex = index;
      }
      return found;
    });
    if (data[foundActivityAtIndex].children && foundItem) {
      data[foundActivityAtIndex].active = true;
      this._setActive(data[foundActivityAtIndex].children);
    }
  }

  /**
   * Deactivate all tree-items without triggering event.
   *
   * @function deactivateAll
   * @public
   */
  deactivateAll = () => {
    let treeItems;
    if (this.data) {
      treeItems = this.shadowRoot.querySelectorAll(`eui-base-v${__VERSION__}-tree-item`);
    } else {
      treeItems = this.querySelectorAll(`eui-base-v${__VERSION__}-tree-item`);
    }
    treeItems.forEach((treeItem) => {
      treeItem.active = false;
    });
  };

  /**
   * Get the currently active tree-item.
   *
   * @function getSelected
   * @public
   * @returns [NodeElement] - array of active tree-items
   */
  getSelected = () => {
    let treeItems;
    if (this.data) {
      treeItems = this.shadowRoot.querySelectorAll(`eui-base-v${__VERSION__}-tree-item[active]`);
    } else {
      treeItems = this.querySelectorAll(`eui-base-v${__VERSION__}-tree-item[active]`);
    }
    return treeItems;
  };

  /**
   * Create the tree componet based on data entered via the data prop.
   *
   * @function _makeTreeFromData
   * @param {Object} treeItem - tree item object
   * @private
   */
  _makeTreeFromData(treeItem) {
    return html`
      <eui-base-v0-tree-item
        class="${this.navigation ? 'tree--navigation' : ''}"
        ?open="${treeItem.open}"
        ?active="${treeItem.active}"
        .itemData=${treeItem}
        >${treeItem.label}
        ${treeItem.children && treeItem.children.map(child => this._makeTreeFromData(child))}
      </eui-base-v0-tree-item>
    `;
  }

  didRender() {
    if (this.navigation) {
      setTimeout(() => {
        const currentTreeItems = this.querySelectorAll('eui-base-v0-tree-item');
        currentTreeItems.forEach(i => i.classList.add('tree--navigation'));
      }, 0);
    }
  }

  /**
   * Hook into the changed props. If data is changed it must be determined if
   * the active tree-item has a parent and if so that too should be set active.
   * Once all parents are found and set active = true the data is mutated.
   *
   * @function didChangeProps
   * @param {Map} changedProps - changed props
   */
  didChangeProps(changedProps) {
    if (changedProps.has('data') && this.data != null) {
      // need to set all parents active = true.
      this._setActive();
      // mutate the data...
      this.data = [...this.data];
    }
  }

  /**
   * Render the tree component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`
      <div class="tree" @deactive-all=${this}>
        <ul class="tree__ul">
          ${!this.data
            ? html`
                <slot></slot>
              `
            : this.data.map(treeItem => this._makeTreeFromData(treeItem))}
        </ul>
      </div>
    `;
  }
}
