/* eslint no-console:0 */
/**
 * Component Dialog is defined as
 * `<eui-base-v[version]-dialog>`
 * Where [version] is replaced with the major version number of the package.
 * The version of the package is found in project.json
 *
 * Imperatively create a dialog component
 * @example
 * let dialog = document.createElement('eui-base-v0-dialog');
 *
 * Declaratively create a dialog component
 * @example
 * <eui-base-v0-dialog></eui-base-v0-dialog>
 *
 * @extends {LitComponent}
 */
import { LitComponent, html } from '@eui/lit-component';
import { definition } from '@eui/component';
import style from './dialog.css';
/**
 * @property {Boolean} fullscreen - true, makes dialog to take full sceen space
 * @property {String} label - sets title of dialog component
 * @property {Boolean} noCancel - true hides the "Cancel" button,
 * false shows the "Cancel" button. Defaults to false
 * @property {Boolean} show - true sets dialog component visible
 * @property {Boolean} warning - true sets dialog component as warning dialog with
 * the cancel button placed on the right
 */
@definition(`eui-base-v${__VERSION__}-dialog`, {
  style,
  props: {
    fullscreen: { attribute: true, type: Boolean },
    label: { attribute: true, type: String },
    noCancel: { attribute: true, type: Boolean },
    show: { attribute: true, type: Boolean },
    warning: { attribute: true, type: Boolean },
  },
})
export default class Dialog extends LitComponent {
  constructor() {
    super();
    this.hideDialog = this.hideDialog.bind(this);
    this.showDialog = this.showDialog.bind(this);
  }

  /**
   * lifecycle hook triggers when component receives
   * a new prop
   *
   * @method didChangeProps
   *
   * @param {Map} changedProps - previous values of the changed props
   */
  didChangeProps() {
      if (this.show) {
        this.classList.add('show');
      } else {
        this.classList.remove('show');
      }
  }

  /** hide the dialog
   * @method hideDialog
   *
   */
  hideDialog() {
    this.classList.remove('show');
    this.bubble('eui-dialog:cancel');
    this.show = false;
  }

  /** show the dialog
   * @method showDialog
   *
   */
  showDialog() {
    this.classList.add('show');
  }

  /**
   * Render the dialog component. This function is called each time a
   * prop changes.
   */
  render() {
    let location;
    try {
      location = window.EUI.Localizer.loc;
    } catch (error) {
      location = {};
    }

    const bottom = this.fullscreen ? '' : html`<div class="dialog__bottom ${this.warning ? 'dialog__bottom--reverse' : ''}">
      ${this.noCancel ? '' : html`<eui-base-v0-button class="cancel" @click="${this.hideDialog}">${location.CANCEL || 'Cancel'}</eui-base-v0-button>`}
      <slot name="bottom">
            </slot>
          </div>`;
    const right = this.fullscreen ? html`<div class="dialog__right">
                  <eui-base-v0-icon class="cross" name="cross" size="14px" @click="${this.hideDialog}"></eui-base-v0-icon>
                  </div>` : '';
    return html`
          <div class="dialog">
            <div class="dialog__top">
              <div class="dialog__title">${this.label}</div>
              ${right}
            </div>
            <div class="dialog__body">
              <slot name="content"></slot>
            </div>
            ${bottom}
          </div>`;
  }
}
