/**
 * Component Pill is defined as
 * `<eui-base-v[version]-pill>`
 *
 * Imperatively create component
 * @example
 * let component = new Pill();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-pill></eui-base-v0-pill>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import style from './pill.css';

/**
 * @property {Boolean} toggle - makes the pill togglable or non-togglable
 * @property {Boolean} dismissable - makes the pill dismissable
 * @property {Boolean} disabled - true disables the component, false enables the component
 * @property {Boolean} on - sets the toggle state of the pill
 * @property {String} icon - name of the icon to display in the pill
 * @property {String} severity - type of severity color to set for the pill
 * @property {String} color - color value to apply to icon inside the pill
 */
@definition(`eui-base-v${__VERSION__}-pill`, {
  style,
  props: {
    toggle: { attribute: true, type: Boolean },
    dismissable: { attribute: true, type: Boolean },
    disabled: { attribute: true, type: Boolean },
    on: { attribute: true, type: Boolean },
    icon: { attribute: true, type: String },
    severity: { attribute: true, type: String },
    color: { attribute: true, type: String },
  },
})
export default class Pill extends LitComponent {
  /**
   * _isIcon determines if there is an icon added to the pill.
   *
   * @function _isIcon
   * @private
   */
  _isIcon = () => this.icon && this.icon.length > 0;

  /**
   * _isSeverity determines if the pill needs to display a severity icon.
   *
   * @function _isSeverity
   * @private
   */
  _isSeverity = () => this.severity && this.severity.length > 0;

  /**
   * _withIcon renders the markup for the icon if it is added to the pill
   *
   * @function _withIcon
   * @private
   */
  _withIcon = () => {
    if (this._isSeverity()) {
      return html`<eui-v0-icon name=${`severity-${this.severity}`} size=12px></eui-v0-icon>`;
    }
    if (this._isIcon()) {
      return html`<eui-v0-icon name=${this.icon} color=${(this.color === null) ? '' : this.color} size=12px></eui-v0-icon>`;
    }
    return null;
  }

  /**
   * Close pill via X icon
   */
  _closePill = (event) => {
    if (event) {
      event.stopPropagation();
    }
    const emittedEvent = this.bubble('dismiss', {}, { cancelable: true });
    if (!emittedEvent.defaultPrevented) {
      this.parentElement.removeChild(this);
    }
  };

  /**
   * Get styles for root element
   *
   * @function _getStyles
   * @private
   */
  _getStyles = () => {
    const pillSeverity = this.severity ? `severity-${this.severity}` : '';
    const pillToggle = this.toggle ? 'toggle' : '';
    return `pill ${pillToggle} ${pillSeverity}`;
  }

  /**
   * Handles the state change of the pill
   */
  handleEvent(event) {
    if (event.type === 'click' && this.toggle) {
      this.on = !this.on;
    }
  }

  /**
   * Renders the pill component. This function is called each time a
   * prop changes.
   *
   * @method render
   */
  render() {
    return html`
    <style>  
      :host {  
        display: inline-block;
      }
      
    </style>
    <span class="${this._getStyles()}" @click="${this}" ?disabled=${this.disabled}>
      ${this._withIcon()}
      <slot></slot>
      ${(this.dismissable) ? html`<eui-v0-icon
        @click=${() => this._closePill()}
        size=10px
        color="var(--text)"
        class="close"
        name="cross"
      ></eui-v0-icon>` : ''}
    </span>
    `;
  }
}
