/**
 * Component TreeItem is defined as
 * `<eui-base-v[version]-tree-item>`
 *
 * Imperatively create component
 * @example
 * let component = new TreeItem();
 *
 * Declaratively create component
 * @example
 * <eui-base-v0-tree-item></eui-base-v0-tree-item>
 *
 * @event itemselect - triggered each time a tree-item is selected
 * @event itemexpand - triggered each time a tree-item is expanded
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import style from './treeItem.css';

/**
 * @property {Boolean} active - true/false sets the tree item selected/unselected
 * @property {Boolean} hasLeaves - this is used to set the tree-item as a branch with children.
 * @property {Object} itemData - holds a data representation of the tree-item.
 * @property {Number} level - the level of nesting [0, 1, 2, 3 ... ]
 * @property {Boolean} open - true/false opens/closes a nested tree
 */
@definition(`eui-base-v${__VERSION__}-tree-item`, {
  style,
  props: {
    active: { attribute: true, type: Boolean },
    hasLeaves: { type: Boolean },
    itemData: { type: Object, default: { active: false } },
    level: { type: Number, default: 0 },
    open: { attribute: true, type: Boolean },
  },
})
export default class TreeItem extends LitComponent {
  didConnect() {
    if (this.parentNode.localName === `eui-base-v${__VERSION__}-tree-item`) {
      this.parentNode.hasLeaves = true;
      this.slot = 'item-slot';
      this.level = this.parentNode.level + 1;
      this.style.setProperty('--tree-item-left-padding', `${this.level}em`);
    }
  }

  /**
   * Called each time a prop changes.
   * set the activity state of the tree-item. If the new active state
   * (this.active) is true, then look for it's parent to also set it active.
   *
   * @function didChangeProps
   * @param {Object} prev - previous state of the component
   */
  didChangeProps(changedProps) {
    if (changedProps.has('active') && this.active && !changedProps.has('itemData')) {
      let currParentNode = this.parentNode;
      while (currParentNode && currParentNode.localName === 'eui-base-v0-tree-item') {
        currParentNode.active = true;
        currParentNode = currParentNode.parentNode;
      }
    }
  }

  /**
   * Move all instances of <eui-base-v[version]-tree-item> to the item-slot
   *
   * @function _moveToItemSlot
   * @private
   */
  _moveToItemSlot() {
    const items = this.children;
    for (let i = 0; i < items.length; i += 1) {
      if (items[i].localName === `eui-base-v${__VERSION__}-tree-item`) {
        items[i].slot = 'item-slot';
        items[i].level = this.level + 1;
        this.hasLeaves = true;
      }
    }
    this.style.setProperty('--tree-item-left-padding', `${this.level}em`);
  }

  /**
   * Handles the click event on a tree-item
   * Set an item active only if it is not a branch
   * (containes nested tree-items) and it is not already active.
   *
   * @function handleEvent
   * @event itemselect - triggered each time a tree-item is selected
   * @event itemexpand - triggered each time a tree-item is expanded
   */
  handleEvent(event) {
    if (event.target.id === 'tree-chevron' && event.type === 'click') {
      event.stopPropagation();
      this.open = !this.open;
      this.itemData.open = this.open;

      this.bubble('chevron-click', {
        label: this.innerText,
        open: this.open,
        data: this.itemData.data,
      });
    }
    if (event.target.id !== 'tree-chevron' && event.type === 'click') {
      if (this.hasLeaves) {
          this.open = !this.open;
          this.itemData.open = this.open;
        this.bubble('itemexpand', this);
      } else if (!this.hasLeaves) {
        if (this.active !== undefined && this.active === false) {
          this.bubble('deactive-all', this);
          this.bubble('itemselect', {
            label: this.innerText,
            data: this.itemData.data,
          });
        }
      }
    }
  }

  /**
   * Create the classList uest to style the span element (tree__item__span)
   *
   * @function _updateClassList
   * @private
   */
  _updateClassList() {
    let classList = 'tree__item__span';

    if (this.hasLeaves) {
      classList += ' tree__item__span__branch';
      if (this.open) {
        classList += ' tree__item__span__branch--open';
      } else {
        classList += ' tree__item__span__branch--closed';
      }
      if (this.active) {
        classList += ' tree__item__span__branch--active';
      }
    } else {
      classList += ' tree__item__span__leaf';
      if (this.active) {
        classList += ' tree__item__span__leaf--active';
      }
    }
    return classList;
  }

  /**
   * Render the <e-tree-item> component. This function is called each time a
   * prop changes.
   */
  render() {
    const spanClassList = this._updateClassList();
    const name = this.open ? 'chevron-down' : 'chevron-right';

    return html`
      <li class="tree__item">
        <span class="${spanClassList}" @click=${this}>
          <eui-base-v0-icon id="tree-chevron" @click=${this} class="icon" name=${name} size="14px"></eui-base-v0-icon>
          <slot></slot>
          ${this.data ? this.data.label : ''}
        </span>
        <ul class="tree__item__ul"><slot name='item-slot'></slot></ul>
      </li>`;
  }
}
